.tools {
  padding-bottom: $pb-section-desctop;
  &__inner {

  }
  &__title {

  }
  &__row {
    display: flex;
    align-items: center;
    margin-bottom: 30px;
  }
  &__img {
    width: 200px;
    min-width: 200px;
    & > img {
      margin: 0 auto 0 0;
    }
  }
  &__text {
    & p {
      margin-bottom: 15px;
      color: #6C6A72;
    }
    & b {
      font-weight: 500;
      color: #0A274F;
      font-size: 18px;
    }
  }
}


@media (min-width: 1200px) {

}
@media (min-width: 992px) and (max-width: 1199px) {

}
@media (min-width: 768px) and (max-width: 991px) {

}
@media (max-width: 767px) {
  .tools {
    padding-bottom: $pb-section-mobile;
    &__text {
      font-size: 14px;
      line-height: 21px;
      & b {
        font-size: 16px;
        line-height: 24px;
      }
    }
    &__img {
      align-self: flex-start;
      width: 170px;
      min-width: 170px;
    }
  }
}
@media (max-width: 480px) {
  .tools {
    &__row {
      display: block;
      margin-bottom: 28px;
    }
    &__text {
      & b {
        margin-bottom: 5px;
        display: inline-block;
      }
    }
    &__img {
      float: left;
      width: 110px;
      min-width: 110px;
      margin-right: 10px;
      margin-bottom: 10px;
      & > img {
        margin: 0;
      }
    }
  }
}
