$right-pd: 125px;
.top {
  background: no-repeat center / cover;
  background-image: url(../img/bg/bg1.jpg);
  margin-bottom: 150px;
  &__inner {
    padding: 120px $right-pd 60px 0;
    position: relative;
  }
  &__title {
    font-weight: 700;
    font-size: 75px;
    line-height: 88px;
    color: $color-accent1;
    max-width: 620px;
    margin-bottom: 35px;
  }
  &__subtitle {
    font-size: 18px;
    line-height: 27px;
    margin-bottom: 50px;
    max-width: 480px;
  }
  &__btn {
    margin-bottom: 60px;
  }
  &__info {
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
  }
  &__info-item {
    max-width: calc(105px + 40px);
    position: relative;
    padding-right: 40px;
    &::after {
      content: '';
      display: block;
      position: absolute;
      top: 0;
      bottom: 0;
      right: 20px;
      width: 2px;
      height: 100%;
      background-color: #ffffff;
    }
    &:last-child {
      padding-right: 0;
      max-width: 115px;
    }
    &:last-child::after {
      display: none;
    }
  }
  &__info-num {
    font-weight: 700;
    font-size: 40px;
    line-height: 27px;
    color: $color-accent1;
    margin-bottom: 8px;
  }
  &__info-text {
    color: #838383;
    line-height: 27px;

  }
  &__float-block {
    position: absolute;
    right: $right-pd;
    top: 30px;
    padding: 15px 38px;
    color: $color-accent2;
    background: rgba(255, 255, 255, 0.7);
    border-radius: 27px;
  }
}




