@media (max-width: 767px) {
  .top {
    margin-bottom: 55px;
    &__inner {
      padding: 35px 0;
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
    &__title {
      font-size: 30px;
      line-height: 35px;
      max-width: 250px;
      margin-bottom: 15px;
    }
    &__subtitle {
      font-size: 14px;
      line-height: 27px;
      max-width: 210px;
      margin-bottom: 20px;
    }
    &__info {
      order: 1;
      flex-direction: column;
      align-items: flex-start;
    }
    &__info-item {
      padding-right: 0;
      max-width: 90px;
      padding-bottom: 30px;
      &::after {
        position: absolute;
        top: auto;
        right: auto;
        bottom: 15px;
        width: 100%;
        height: 2px;
      }
    }
    &__info-num {
      font-size: 30px;
      margin-bottom: 5px;
    }
    &__info-text {
      font-size: 14px;
      line-height: 21px;
    }
    &__btn {
      order: 2;
      margin-bottom: 0;
    }
    &__float-block {
      display: none;
    }
  }
}
@media (max-width: 480px) {
  .top {
    background-image: url(../img/bg/bg-mob.jpg);
    background-position: center 70%;
  }
  
}
