.differences {
  padding-bottom: $pb-section-desctop;
  &__inner {

  }
  &__title {
    margin-bottom: 80px;
  }
  &__table {
    background: #F7F8FF;
    border-radius: 10px;
  }
  &__row {
    display: flex;
    justify-content: space-between;
    position: relative;
    /* нижняя граница строки */
    &::after {
      content: '';
      background: #F1F1F2;
      display: block;
      height: 1px;
      width: calc(100% - 60px);
      position: absolute;
      z-index: 1;
      left: 30px;
      bottom: 0;
    }
    &:last-of-type::after {
      display: none;
    }
    /* ========== */

    /* верхняя и нижняя плашка в средней колонке */
    &:first-of-type .differences__cell:nth-of-type(2)::before, 
    &:last-of-type .differences__cell:nth-of-type(2)::before {
      content: '';
      position: absolute;
      display: block;
      background-color: $color-accent2;
      width: 100%;
      height: 1.5em;
      left: 0;
      right: 0;
    }
    &:first-of-type .differences__cell:nth-of-type(2)::before {
      top: -1.5em;
      border-top-left-radius: 21px;
      border-top-right-radius: 21px;
    }
    &:last-of-type .differences__cell:nth-of-type(2)::before {
      bottom: -1.5em;
      border-bottom-left-radius: 21px;
      border-bottom-right-radius: 21px;
      // box-shadow: 0px 10px 40px 0px rgba(202, 213, 255, 0.7);  //TODO добавить тень ко второй колонке
    }
    /* ======= */

    /* нижняя граница каждого элемента во второй колонке */
    & .differences__cell:nth-of-type(2)::after {
      content: '';
      display: block;
      height: 1px;
      width: 84%;
      position: absolute;
      bottom: 0;
      left: 8%;
      background: #E7ECFF;
    }
    &:last-of-type .differences__cell:nth-of-type(2)::after {
      display: none;
    }
    /* ========== */
  }
  &__cell {
    padding: 25px 50px;
    position: relative;
    z-index: 2;
    &:nth-of-type(1) {
      flex: 0 0 38%;
      color: #6C6A72;
    }
    &:nth-of-type(2) {
      background-color: $color-accent2;
      flex: 0 0 31%;
      color: #FFFFFF;
    }
    &:nth-of-type(3) {
      flex: 0 0 31%;
      color: #6C6A72;
    }
  }
}



