@media (min-width: 768px) and (max-width: 991px) {

}
@media (max-width: 767px) {
  .differences {
    padding-bottom: $pb-section-mobile;
    &__table {
      width: calc(100% + 20px);
      margin-left: -10px;
    }
    &__row {
      flex-wrap: wrap;
      &:first-of-type {
        background-color: #FFFFFF;
      }
      &:first-of-type .differences__cell:first-of-type {  //пустая ячейка
        display: none;
      }
      & .differences__cell:nth-of-type(1) {   //заголовки
        flex-basis: 100%;
        background-color: $color-accent2;
        color: #FFFFFF;
        padding: 0 10px 12px;
      }
      &:first-of-type .differences__cell:nth-of-type(2),  //ячейки "психолог" и "диетолог"
      &:first-of-type .differences__cell:nth-of-type(3) {
        flex-basis: 50%;
        padding: 15px;
        padding-left: 20px;
        background-color: #FFFFFF;
        font-size: 16px;
        height: 55px;
        border-right: none;
      }
      &:first-of-type .differences__cell:nth-of-type(2) {
        color: $color-accent2;
      }
      &:first-of-type .differences__cell:nth-of-type(3) {
        color: $color-subtitle;
        opacity: 0.5;
      }
      
      &:first-of-type .differences__cell:nth-of-type(2)::before,  //плашки во второй колонке
      &:last-of-type .differences__cell:nth-of-type(2)::before {
        display: none;
      }
      & .differences__cell:nth-of-type(2)::after {
        display: none;
      }
      
    }
    &__cell {
      &:nth-of-type(1) {
        font-size: 12px;
        line-height: 21px;
      }
      &:nth-of-type(2) {
        background-color: transparent;
        color: rgb(108, 106, 114);
        border-right: 0.5px solid #E7ECFF;
      }
      &:nth-of-type(3) {
        color: rgba(108, 106, 114, 0.5);
      }
      &:nth-of-type(2),  //ответы
      &:nth-of-type(3) {
        flex: 0 0 50%;
        padding: 7px 10px;
        height: 70px;
        font-size: 12px;
        line-height: 18px;
      }
    }
  }
}
