@media (max-width: 767px) {
  .reason {
    padding-bottom: $pb-section-mobile;
    &__content {
      padding: 0;
    }
    &__text {
      padding: 20px 0;
      font-size: 14px;
      line-height: 21px;
      border-radius: 0;
      & > p:last-child {
        margin-bottom: 0;
      }
      &::after {
        display: none;
      }
    }
    &__img {
      display: none;
    }
  }
}
