.leading {
  padding-bottom: $pb-section-desctop;
  &__inner {
    // position: relative;
  }
  &__title {
    margin-bottom: 30px;
  }
  &__content {
    position: relative;
    padding-right: 130px;
  }
  &__text {
    position: relative;
    background-color: #F6F8FF;
    line-height: 24px;
    border-top-right-radius: 150px;
    padding: 30px 400px 30px 0;
    &::after {
      content: '';
      display: block;
      position: absolute;
      z-index: -1;
      background-color: #F6F8FF;
      width: 50%;
      left: -100px;
      top: 0;
      height: 100%;
    }
  }
  &__intro {
    margin-bottom: 60px;
    max-width: 500px;
    color: $color-subtitle;
  }
  &__row {
    margin-bottom: 55px;
    font-size: 18px;
    font-weight: 500;
    color: #6C6A72;
  }
  &__accent {
    color: #3E4DA6;
    font-size: 35px;
    font-weight: 700;
  }
  &__img {
    position: absolute;
    right: 0;
    bottom: 0;
    max-width: 350px;
  }
}



