@media (max-width: 767px) {
  .leading {
    padding-bottom: $pb-section-mobile;
    &__content {
      display: flex;
      flex-direction: column;
      padding-right: 0;
    }
    &__text {
      // padding-right: 0;
      padding: 28px 35px 20px 10px;
      border-radius: 0;
      width: calc(100% + 20px);
      margin-left: -10px;
      font-size: 14px;
      line-height: 21px;
      &::after {
        display: none;
      }
    }
    &__row {
      margin-bottom: 20px;
      font-size: 14px;
      line-height: 21px;
      &:last-child {
        margin-bottom: 0;
      }
    }
    &__accent {
      font-size: 30px;
      width: 100%;
      display: inline-block;
      margin-bottom: 10px;
      text-align: left;
    }
    &__img {
      position: static;
      order: -1;
      margin-bottom: 30px;
      align-self: flex-end;
    }
  }
}
