@media (max-width: 767px) {
  .header {
    &__logo {
      max-width: 235px;
    }
    &__top {
      padding: 0;
      height: 40px;
      align-items: center;
      display: flex;
      position: relative;
    }
    &__tel {
      font-size: 14px;
      &::before {
        width: 18px;
        height: 18px;
      }
    }
    &__geo {
      display: none;
    }
    &__middle {
      padding: 5px 0;
    }
    &__bottom {
      position: absolute;
      z-index: 5;
      right: 0;
      top: 40px;
      background-color: #ffffff;
      max-width: 240px;
      width: 100%;
      padding: 40px 25px;
      transition: .25s;
      transform: translateX(100%);
      &.active {
        transform: translateX(0);
      }
    }
  }
  .menu {
    flex-direction: column;
    align-items: flex-start;
    &__item {
      margin: 0;
      margin-bottom: 35px;
      color: #0A274F;
      &:last-child {
        margin-bottom: 0;
      }
    }
  }
  .burger {
    display: flex;
  }
}
