.request {
  padding-bottom: $pb-section-desctop;
  &__inner {}
  &__title {
    margin-bottom: 30px;
  }
  &__subtitle {
    font-weight: 500;
    font-size: 18px;
    color: $color-subtitle;
    margin-bottom: 45px;
  }
  &__form {

  }
  &__row {
    display: flex;
    // justify-content: space-between;
    align-items: flex-end;
    margin-bottom: 25px;
  }
  &__box {
    position: relative;
    padding-top: 16px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    max-width: 295px;
    width: 100%;
    margin-right: 35px;
    &::before {
      content: '';
      display: block;
      width: 111px;
      height: 94px;
      position: absolute;
      z-index: -1;
      top: -16px;
      left: -20px;
      background: no-repeat center / contain;
    }
    &:nth-of-type(1)::before {
      background-image: url(../img/bg/rhombus-blue.svg);
    }
    &:nth-of-type(2)::before {
      background-image: url(../img/bg/rhombus-gray.svg);
    }
  }
  &__label {
    margin-bottom: 3px;
  }
  &__input {
    max-width: none;
    width: 100%;
    padding: 20px 25px 15px;
    box-shadow: 3px 4px 30px rgba(62, 77, 166, 0.1);
    border: none;
    border-radius: 10px;
    &::placeholder {
      color: rgba(163, 172, 186, 0.7);
    }
  }
  &__btn {}
  &__policy {
    font-size: 11px;
  }
  &__policy-label {
    position: relative;
    cursor: pointer;
    &::before, &::after {
      cursor: pointer;
    }
    &::before {
      content: '';
      display: block;
      width: 10px;
      height: 10px;
      border: 0.5px solid $color-accent1;
      border-radius: 1px;
      position: absolute;
      top: 50%;
      margin-top: -5px;
    }
    &::after {
      content: '';
      display: none;
      width: 10px;
      height: 10px;
      position: absolute;
      top: 50%;
      margin-top: -5px;
      background: no-repeat center / contain;
      background-image: url(../img/icon/check.svg);
    }
    &.checked::after {
      display: block;
    }
  }
  &__cb {
    margin: 0;
    padding: 0;
    opacity: 0;
    visibility: hidden;
  }
}




