@media (min-width: 768px) and (max-width: 991px) {

}
@media (max-width: 767px) {
  .request {
    padding-bottom: $pb-section-mobile;
    &__subtitle {
      font-size: 16px;
      line-height: 24px;
      margin-bottom: 40px;
    }
  }
}
@media (max-width: 480px) {
  .request {
    &__form {

    }
    &__row {
      flex-direction: column;
      margin-bottom: 0;
    }
    &__box {
      max-width: none;
      margin-right: 0;
      padding-top: 0;
      margin-bottom: 20px;
      &:nth-of-type(2) {
        margin-bottom: 30px;
      }
      &:last-child {
        margin-bottom: 0;
      }
      &::before {
        display: none;
      }
    }
    &__label {
      margin-bottom: 6px;
    }
    &__input {
      padding: 20px;
    }
    
    &__policy {
      display: none;
    }
    &__btn {
      max-width: none;
      width: 100%;
    }
  }
}
