@media (max-width: 767px) {
  .chart {
    padding-bottom: $pb-section-mobile;
    &__content {
      flex-direction: column;
      background: none;
      &::after {
        display: none;
      }
    }
    &__board {
      flex-basis: 100%;
      max-width: 100%;
      background-color: #ffffff;
      margin-bottom: 30px;
    }
    &__text {
      flex-basis: calc(100% + 20px);
      max-width: calc(100% + 20px);
      margin-left: -10px;
      background: #F7F8FF;
      padding: 20px 10px;
      font-size: 14px;
      line-height: 21px;
      
      & b {
        font-size: 15px;
      }
    }
  }

  .chart-board {  //TODO проверить еще раз
    padding: 25px 15px 40px 25px;
    // height: 90vw;
    &__title {
      font-size: 15px;
    }
    &__box {
      height: 65vw;
      &::before {
        left: -12px;
        top: -10px;
      }
      &::after {
        bottom: -20px;
      }
    }
    &__icon { 

      top: 40%;
      &::after {
        font-size: 14px;
      }
    }
  }
}
@media (max-width: 480px) {
  .chart-board {
    &__icon {
      width: 25px;
      height: 25px;
    }
  }
}
